body {
  margin: 0%;
  padding: 0%;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #00a5bb;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
#root {
  margin: 0;
  padding: 0;
}

.App {
  margin: 0%;
}

.custom-div {
  background-color: #fff;
  margin: 0;
  padding: 10px;
  text-align: center;
}

.title {
  font-weight: 600;
}
.timer-div {
  background-position: center;

  text-align: center;

  position: relative;
}

.timer {
  text-align: center;
  top: 5%;
  left: 50%;
  transform: translate(-50%, -5%);
  position: absolute;
  font-size: 8em;
  color: #fff;
  font-weight: 400;
  text-shadow: 4px 4px 10px #00000096;
}

.btn-group {
  margin-top: 45vh;
  left: 50%;
  transform: translateX(-50%);
  position: absolute;
}

.btn {
  margin-bottom: 20px;
  padding: 10px;
  border: 0;
  width: 200px;
  cursor: pointer;
  border-radius: 10px;
  height: 50px;
  text-transform: uppercase;
  font-size: 20px;
  color: #fff;
  background-color: rgb(38, 17, 158);
  transition: all 0.3s;
}

.btn:hover {
  transform: translateY(-5px);
  box-shadow: 0px 2px 4px 0px #00000096;
}

.btn:active {
  transform: translateY(-2px);
  box-shadow: 0px 2px 10px 0px #00000096;
}
